<template>
  <div class="log-communicationlog">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="log_communicationlog_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="reload">刷新</el-button>
        <span class="colorNotice">（ 注意：该功能与平台使用无关,仅用于协助售后维护人员发现问题及解决问题。）</span>
      </template>
      <!-- 用户名 -->
      <template v-slot:user_name="props">
        {{ props.data.row.user.show_name }}
      </template>
      <!-- 用户名 -->
      <template v-slot:host="props">
        {{ props.data.row.host.name }}
      </template>
      <!-- 操作IP -->
      <template v-slot:ctime="props">
        {{ props.data.row.ctime | formatDate }}
      </template>
      <!-- 状态 -->
      <template v-slot:status="props">
         <el-tag
          type="success"
          disable-transitions>等待系统重新下发</el-tag>
      </template>
    </g-el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      url: "/cmds/history",
      type: "get",
      cloumns: [
        {
          type: "index",
          fixed: "left",
          label: "序号",
          indexMethod: function(index) {
            return index + 1;
          },
          width: "60px"
        },
        {
          label: "操作用户",
          sortable: true,
          prop: "user_name",
          width: "150px",
          isTemplate: true,
          templateName: "user_name"
        },
        // {
        //   label: "模块名称",
        //   prop: "module",
        //   sortable: true,
        //   width: "150px"
        // },
        {
          label: "描述",
          prop: "description",
          sortable: true,
          minWidth: "200px"
        },
        {
          label: "失败原因",
          prop: "err_msg",
          sortable: true,
          minWidth: "200px"
        },
        {
          label: "对应主机",
          prop: "host",
          sortable: true,
          width: "200px",
          isTemplate: true,
          templateName: "host"
        },
        {
          label: "操作时间",
          prop: "ctime",
          sortable: true,
          width: "200px",
          isTemplate: true,
          templateName: "ctime"
        },
        {
          label: "状态",
          prop: "status",
          width: "150px",
          isTemplate: true,
          templateName: "status"
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      icon: {
        stateIcon: this.$icons.stateIcon
      },
      selectData: []
    };
  },
  filters: {
    formatDate(val) {
      let time = val;
      if (!isNaN(val)) {
        time = new Date(val * 1000);
        time = time.format("yyyy年MM月dd日 HH:mm:ss");
      }
      return time;
    }
  },
  methods: {
    reload() {
      this.$refs.log_communicationlog_table.reload();
    },
    search() {
      this.params.page = 1;
      this.reload();
    },
    selectChange(select) {
      this.selectData = select;
    }
  }
};
</script>
<style>
.log-communicationlog {
  width: 100%;
  margin: 10px;
  border: 1px solid #ebeef5;
}
.log-communicationlog .el-table td,
.log-communicationlog .el-table th.is-leaf {
  border-right: 1px solid #ebeef5;
}
.log-communicationlog .el-table th {
  background: #f5f7fa;
}
</style>



